
import { defineComponent, reactive, ref, onMounted } from 'vue'
import BackendApi from '../assets/js/api/backendApi';

export default defineComponent({
  setup() {
    const tableData = reactive([]);
    const search = reactive({
      city: null,
      startDt: null,
      endDt: null,
    });
    const searching = reactive({
      city: null,
      startDt: null,
      endDt: null,
    });
    const cityList = reactive([]);
    const loading = ref(false);
    const page = ref(1);
    const totalElement = ref(10);

    const getData = async () => {
      loading.value = true;
      tableData.splice(0);
      const res = await BackendApi.getData({
        page: page.value,
        ...searching,
      });
      if (res.code === 200) {
        res.data.content.forEach((e) => {
          tableData.push(e);
        })
        // Object.assign(tableData, res.data.content);
        totalElement.value = res.data.totalElement
      }
      loading.value = false;
    };

    const getCityList = async () => {
      const res = await BackendApi.getCityList();
      if (res.code === 200) {
        res.data.forEach((e) => {
          cityList.push(e);
        })
      }
    }

    const getSearch = () => {
      Object.keys(search).forEach((key) => {
        searching[key] = search[key];
      });
      page.value = 1;
      getData();
    }

    const clear = () => {
      reset();
      getData();
    }

    const reset = () => {
      Object.keys(search).forEach((key) => {
        search[key] = null;
        searching[key] = null;
      });
      page.value = 1;
    }

    const currentChange = (e) => {
      page.value = e;
      getData();
    };

    const disabledStartDate = (time) => {
      return !search.endDt ? false : time.getTime() > new Date(search.endDt).getTime();
    }

    const disabledEndDate = (time) => {
      return time.getTime() > Date.now() || time.getTime() < new Date(search.startDt).getTime();
    }

    onMounted(() => {
      getCityList();
      getData();
    });

    return {
      loading,
      tableData,
      cityList,
      search,
      searching,
      totalElement,
      page,
      currentChange,
      getSearch,
      clear,
      disabledStartDate,
      disabledEndDate
    }
  }
})
